import _ from 'lodash';
import React from 'react';
import { graphql } from 'gatsby';
import Layout from "../components/layout";
import Breadcrumb from '../components/breadcrumb';
import Headtohead from '../components/headtohead';
import Gamelogs from '../components/gamelogs';

class IndexPage extends React.Component {
    constructor(props) {
        super(props);

        this.allGames = [];
        this.initialTeam = {
            record: '-',
            name: '',
            ownerId: '',
            image: 'https://fantasy-franchise.com/img/DEF.png',
            currentstreak: '',
            highestwinstreak: '',
            highestlosestreak: '',
            totalpoints: '',
            netpoints: '',
            hightestscore: '',
            lowestscore: '',
            biggestwinmargin: '',
            smallestwinmargin: ''
        };
        this.state = {
            owners: [...props.data.allDataJson.edges[0].node.owners],
            teamA: { ...this.initialTeam },
            teamB: { ...this.initialTeam },
            gamesLength: false
		}
		this.playedGames = [];
    }

    teamChangeHandler = event => {
        var owners = [...this.state.owners];
        var curTeam = {};

        // update actual state with modified object
        if (event.target.name === 'teama') {
            curTeam = _.find(owners, ['id', event.target.value]);
            curTeam.record = '-';
            this.setState({ teamA: curTeam }, () => this.matchTeams());
        } else {
            curTeam = _.find(owners, ['id', event.target.value]);
            curTeam.record = '-';
            this.setState({ teamB: curTeam }, () => this.matchTeams());
        }
    };

    matchTeams = () => {
        console.log('Matching teams', this.state);
        if (this.state.teamA.id && this.state.teamB.id) {
            console.log('matched');
            this.setState({ gamesLength: true });
            const teamAlocal = { ...this.state.teamA };
            const teamBlocal = { ...this.state.teamB };
            const localplayedGames = [];
            const winsA = [];
            const winsB = [];

            // get matching games
            this.state.allGames.forEach((game, indi) => {
                if (
                    (game.team_a[0].ownerId === teamAlocal.id ||
                        game.team_a[0].ownerId === teamBlocal.id) &&
                    (game.team_b[0].ownerId === teamAlocal.id ||
                        game.team_b[0].ownerId === teamBlocal.id)
                ) {
                    localplayedGames.push(game);
                }
            });
            this.setState({ playedGames: localplayedGames }, () =>
                console.log('played games', this.state.playedGames)
            );

            // get winners
            localplayedGames.forEach((game, indi) => {
                if (
                    (game.team_a[0].ownerId === teamAlocal.id &&
                        parseFloat(game.team_a[0].score) >
                            parseFloat(game.team_b[0].score)) ||
                    (game.team_b[0].ownerId === teamAlocal.id &&
                        parseFloat(game.team_b[0].score) >
                            parseFloat(game.team_a[0].score))
                ) {
                    winsA.push(game);
                } else if (
                    (game.team_a[0].ownerId === teamBlocal.id &&
                        parseFloat(game.team_a[0].score) >
                            parseFloat(game.team_b[0].score)) ||
                    (game.team_b[0].ownerId === teamBlocal.id &&
                        parseFloat(game.team_b[0].score) >
                            parseFloat(game.team_a[0].score))
                ) {
                    winsB.push(game);
                }
            });

            // map new values
            teamAlocal.record = parseInt(winsA.length);
            teamBlocal.record = parseInt(winsB.length);

            for (var ii = 0; ii < localplayedGames.length; ii++) {
                const week = localplayedGames[ii].week.split(' ');

                localplayedGames[ii].date =
                    '01/' + week[1] + '/' + parseInt(localplayedGames[ii].year);
            }

            localplayedGames.sort(function(a, b) {
                a = new Date(a.date);
                b = new Date(b.date);
                return a > b ? -1 : a < b ? 1 : 0;
            });

            //stat
            const stat = (games, team) => {

                var i,
                    winStreak = 0,
                    lossStreak = 0,
                    highMarginA = 0,
                    highMarginB = 0,
                    lowMarginA = 10000,
                    lowMarginB = 10000,
                    length = games.length,
                    stats = {
                        highestStreak: 0,
                        lowestStreak: 0,
                        currentStreak: 0,
                        highestScore: 0,
                        lowestScore: 10000,
                        highestMargin: 0,
                        lowestMargin: 10000,
                        totalPoints: 0
                    };

                for (i = 0; i < length; i++) {
                    // check the value of the current entry against the last
                    if (
                        (games[i].team_a[0].ownerId === team.id &&
                            parseFloat(games[i].team_a[0].score) >
                                parseFloat(games[i].team_b[0].score)) ||
                        (games[i].team_b[0].ownerId === team.id &&
                            parseFloat(games[i].team_b[0].score) >
                                parseFloat(games[i].team_a[0].score))
                    ) {
                        //check = 1,
                        lowMarginA =
                            parseFloat(games[i].team_a[0].score) -
                            parseFloat(games[i].team_b[0].score);
                        lowMarginB =
                            parseFloat(games[i].team_b[0].score) -
                            parseFloat(games[i].team_a[0].score);
                        winStreak++;
                        lossStreak = 0;
                    } else if (
                        (games[i].team_a[0].ownerId === team.id &&
                            parseFloat(games[i].team_a[0].score) <
                                parseFloat(games[i].team_b[0].score)) ||
                        (games[i].team_b[0].ownerId === team.id &&
                            parseFloat(games[i].team_b[0].score) <
                                parseFloat(games[i].team_a[0].score))
                    ) {
                        //check = ''
                        lossStreak++;
                        winStreak = 0;
                    } else {
                        lossStreak = 0;
                        winStreak = 0;
                    }

                    stats.currentStreak = winStreak;
                    // set the master stats var
                    if (winStreak > stats.highestStreak) {
                        stats.highestStreak = winStreak;
                    }

                    if (lossStreak > stats.lowestStreak) {
                        stats.lowestStreak = lossStreak;
                    }

                    // Check Highest score
                    if (
                        parseFloat(games[i].team_a[0].score) >
                            stats.highestScore &&
                        games[i].team_a[0].ownerId === team.id
                    ) {
                        stats.highestScore = parseFloat(
                            games[i].team_a[0].score
                        );
                    }

                    if (
                        parseFloat(games[i].team_b[0].score) >
                            stats.highestScore &&
                        games[i].team_b[0].ownerId === team.id
                    ) {
                        stats.highestScore = parseFloat(
                            games[i].team_b[0].score
                        );
                    }

                    // Check lowest score
                    if (parseFloat(games[i].team_a[0].score) < stats.lowestScore &&
                        games[i].team_a[0].ownerId === team.id
                    ) {
                        stats.lowestScore = parseFloat(
                            games[i].team_a[0].score
                        );
                    }

                    if (parseFloat(games[i].team_b[0].score) < stats.lowestScore &&
                        games[i].team_b[0].ownerId === team.id
                    ) {
                        stats.lowestScore = parseFloat(
                            games[i].team_b[0].score
                        );
                    }

                    // Check Highest margin
                    highMarginA =
                        parseFloat(games[i].team_a[0].score) -
                        parseFloat(games[i].team_b[0].score);
                    highMarginB =
                        parseFloat(games[i].team_b[0].score) -
                        parseFloat(games[i].team_a[0].score);

                    if (
                        highMarginA > stats.highestMargin &&
                        games[i].team_a[0].ownerId === team.id
                    ) {
                        stats.highestMargin = highMarginA;
                    }

                    if (
                        highMarginB > stats.highestMargin &&
                        games[i].team_b[0].ownerId === team.id
                    ) {
                        stats.highestMargin = highMarginB;
                    }

                    // Check Lowest margin
                    if (
                        lowMarginA < stats.lowestMargin &&
                        games[i].team_a[0].ownerId === team.id &&
                        lowMarginA > 0
                    ) {
                        stats.lowestMargin = lowMarginA;
                    }

                    if (
                        lowMarginB < stats.lowestMargin &&
                        games[i].team_b[0].ownerId === team.id &&
                        lowMarginB > 0
                    ) {
                        stats.lowestMargin = lowMarginB;
                    }

                    if (games[i].team_a[0].ownerId === team.id) {
                        stats.totalPoints += parseFloat(
                            games[i].team_a[0].score
                        );
                    }
                    if (games[i].team_b[0].ownerId === team.id) {
                        stats.totalPoints += parseFloat(
                            games[i].team_b[0].score
                        );
                    }
                }

                return stats;
            };

            const teamAstreak = stat(
                localplayedGames.slice(0).reverse(),
                teamAlocal
            );
           
            teamAlocal.currentstreak = teamAstreak.currentStreak;
            teamAlocal.highestwinstreak = teamAstreak.highestStreak;
            teamAlocal.highestlosestreak = teamAstreak.lowestStreak;
            teamAlocal.highestScore = teamAstreak.highestScore.toFixed(2);
            if (teamAstreak.lowestScore === 10000) {
                teamAlocal.lowestScore = 0;
            } else {
                teamAlocal.lowestScore = teamAstreak.lowestScore.toFixed(2);
            }
            teamAlocal.highestMargin = teamAstreak.highestMargin.toFixed(2);
            if (teamAstreak.lowestMargin === 10000) {
                teamAlocal.lowestMargin = 0;
            } else {
                teamAlocal.lowestMargin = teamAstreak.lowestMargin.toFixed(2);
            }
            teamAlocal.totalPoints = teamAstreak.totalPoints.toFixed(2);

            const teamBstreak = stat(
                localplayedGames.slice(0).reverse(),
                teamBlocal
            );

            teamBlocal.currentstreak = teamBstreak.currentStreak;
            teamBlocal.highestwinstreak = teamBstreak.highestStreak;
            teamBlocal.highestlosestreak = teamBstreak.lowestStreak;
            teamBlocal.highestScore = teamBstreak.highestScore.toFixed(2);
            if (teamBstreak.lowestScore === 10000) {
                teamBlocal.lowestScore = 0;
            } else {
                teamBlocal.lowestScore = teamBstreak.lowestScore.toFixed(2);
            }
            teamBlocal.highestMargin = teamBstreak.highestMargin.toFixed(2);
            if (teamBstreak.lowestMargin === 10000) {
                teamBlocal.lowestMargin = 0;
            } else {
                teamBlocal.lowestMargin = teamBstreak.lowestMargin.toFixed(2);
            }
            teamBlocal.totalPoints = teamBstreak.totalPoints.toFixed(2);

            //total points

            /*
            const getTotalPoints = team => {
                localplayedGames.map((game, indix) => {});
            };
            */

            this.setState({ teamA: teamAlocal });
            this.setState({ teamB: teamBlocal }, () =>
                console.log('teamssss', teamAlocal, teamBlocal)
            );
        } else {
            console.log('no match');
            this.setState({ gamesLength: false });
            this.setState({ playedGames: [] });
        }
    };

    componentDidMount() {
        const years = this.props.data.allDataJson.edges[0].node.years;
        const owners = this.props.data.allDataJson.edges[0].node.owners;
        var games = [];
        years.forEach((year) => {
            year.weeks_games.forEach((week) => {
                games.push(...week.games);
            });
        });
        this.setState({ owners: owners })
        this.setState({ allGames: games });
  
    }

    render() {
        return (
            <Layout >
            <main className="container-fluid headtohead">
                <Breadcrumb title="Head to Head" />

                <div className="row">
                    <div className="col-lg-7">
                        <Headtohead
                            owners={this.state.owners}
                            triggerhappy={event =>
                                this.teamChangeHandler(event)
                            }
                            teama={this.state.teamA}
                            teamb={this.state.teamB}
                            games={this.state.gamesLength}
                        />
                    </div>
                    <div className="col-lg-5">
                        <Gamelogs data={this.state.playedGames} />
                    </div>
                </div>
            </main>
            </Layout>
        );
    }
}
export default IndexPage;

export const pageQuery = graphql`
    query HeadtoheadQuery {
        allDataJson {
            edges {
                node {
                    owners {
						name
						fantasyname
						id
						image
						status
					}
                    years {
                        weeks_games {
                            games {
                                week
                                playoffTierType
                                team_a {
                                    name
                                    owner
                                    ownerId
                                    logo
                                    score
                                    rank
                                }
                                team_b {
                                    name
                                    owner
                                    ownerId
                                    logo
                                    score
                                    rank
                                }
                                year
                            }
                        }
                    }
                }
            }
        }
    }
`;
